import React from 'react';
import PropTypes from 'prop-types';
import './Settings.css';

class Setttings extends React.Component {

    constructor(props) {
        super();
        this.openModal = this.openModal.bind(this);
        this.closeModal = this.closeModal.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    componentDidMount() {

        this.modal = window.$('#settingsModal').modal({
            show: false,
        });
    }


    openModal() {

        this.modal.modal('show');
    }

    closeModal() {

        this.modal.modal('hide');

    }

    handleSubmit(e) {

        e.preventDefault();

        this.closeModal();
    }


    render() {

        const {difficulty, gameType, changeDifficulty, changeGameType} = this.props;
        return (
            <div>
                <button

                    id={'settingsButton'}
                    className={'btn btn-primary d-flex justify-content-center align-items-center'}
                    onClick={e => this.openModal(e)}
                >
                    <i className="fas fa-wrench"></i>
                </button>

                <div className="modal fade" id={'settingsModal'} tabIndex="-1" role="dialog">
                    <div className="modal-dialog" role="document">
                        <div className="modal-content">
                            <div className="modal-header">
                                <h5 className="modal-title text-dark">Settings</h5>
                                <button onClick={this.closeModal} className={'btn btn-default'}>
                                    <i className={'fa fa-times'}></i>
                                </button>
                            </div>
                            <div className="modal-body">
                                <div className={'modal-body'}>
                                    <form name={'settings'} onSubmit={e => this.handleSubmit(e)}>
                                        <div className={'form-group'}>
                                            <label htmlFor={'difficulty'}>Difficulty</label>
                                            <select
                                                name={'difficulty'}
                                                id={'difficulty'}
                                                className={'form-control'}
                                                value={difficulty} onChange={e => changeDifficulty(e)}>
                                                <option value={'easy'}>easy</option>
                                                <option value={'medium'}>medium</option>
                                                <option value={'hard'}>hard</option>
                                            </select>
                                        </div>
                                        {/*<div className={'form-group'}>*/}
                                        {/*<label htmlFor={'gameType'}>Game Type</label>*/}
                                        {/*<select*/}
                                        {/*id={'gameType'}*/}
                                        {/*name={'gameType'}*/}
                                        {/*className={'form-control'}*/}
                                        {/*value={gameType} onChange={e => changeGameType(e)}>*/}
                                        {/*<option>1 player</option>*/}
                                        {/*<option>2 player</option>*/}
                                        {/*</select>*/}
                                        {/*</div>*/}
                                        <button type={'submit'}
                                                className={'btn btn-primary mt-3'}
                                        >Save and Close
                                        </button>


                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        );
    }
}

Setttings.propTypes = {};
Setttings.defaultProps = {};

export default Setttings;



